// ** Store Imports
import { handleLastMenuRemember } from "@store/layout"
import { useDispatch, useSelector } from "react-redux"

export const useLastMenuRemember = () => {
  // ** Hooks
  const dispatch = useDispatch()
  const store = useSelector((state) => state.layout)

  const setLastMenuRemember = (type) => {
    dispatch(handleLastMenuRemember(type))
  }

  return { setLastMenuRemember, lastMenuRemember: store.lastMenuRemember }
}
