import { useEffect, useState } from "react"
import { Star, User } from "react-feather"
// ** Reactstrap Imports
import { Badge, NavItem, NavLink } from "reactstrap"

// ** Utils
import { isUserLoggedIn } from "@utils"

const SuperOrDevBadge = () => {
  // ** State
  const [userData, setUserData] = useState(null)

  //** ComponentDidMount
  useEffect(() => {
    if (isUserLoggedIn() !== null) {
      setUserData(JSON.parse(localStorage.getItem("userData")))
    }
  }, [])

  if (userData?.isDeveloper || userData?.isSuperUser) {
    return (
      <NavItem className="d-lg-flex d-none">
        <NavLink className="nav-link-style">
          <Badge
            color={
              userData.isSuperUser
                ? "danger"
                : userData.isDeveloper
                ? "info"
                : ""
            }
          >
            <Star className="align-middle me-25" />
            <span className="align-middle">
              {userData.isSuperUser
                ? "SuperUser"
                : userData.isDeveloper
                ? "Developer"
                : ""}
            </span>
          </Badge>
        </NavLink>
      </NavItem>
    )
  } else {
    return null
  }
}

export default SuperOrDevBadge
